.TimelineBlock_marker--year {
  left: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 40px;
  height: 2px;
}

.TimelineYearBlock_year {
  position: relative;
  margin-left: 60px;
}

.TimelineYearBlock_year h2 {
  font-size: 30px;
}


