.TimelineBlock_marker--content {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  left: 10px;
  top: 10px;
  -webkit-box-shadow: 0 0 0 4px rgba(0, 0, 0, 0.2);
          box-shadow: 0 0 0 4px rgba(0, 0, 0, 0.2);
  -webkit-transition-property: -webkit-box-shadow;
  transition-property: -webkit-box-shadow;
  -o-transition-property: box-shadow;
  transition-property: box-shadow;
  transition-property: box-shadow, -webkit-box-shadow;
  -webkit-transition-duration: 0.3s;
       -o-transition-duration: 0.3s;
          transition-duration: 0.3s;
}

.TimelineBlock:hover .TimelineBlock_marker--content {
  -webkit-box-shadow: 0 0 0 4px rgba(255, 255, 255, 0.25);
          box-shadow: 0 0 0 4px rgba(255, 255, 255, 0.25);
}

.TimelineContentBlock_content {
  position: relative;
  margin-left: 60px;
  border-radius: 0.25em;
}

.TimelineContentBlock_content:after {
  /* clearfix */
  content: "";
  display: table;
  clear: both;
}

.TimelineContentBlock_content::before {
  /* triangle next to content block */
  content: '';
  position: absolute;
  top: 13px;
  right: 100%;
  height: 0;
  width: 0;
  border: 7px solid transparent;
  border-right: 7px solid rgba(255, 255, 255, 0.15);
}

.TimelineContentBlock_content h2 {
  color: white;
}

.TimelineContentBlock_content p {
  margin: 1em 0 0 0;
  line-height: 1.6;
  overflow: hidden;
}


