.Timeline_scrollbar {
  background-color: rgba(255, 159, 0);
  border-radius: 6px;
}

.Timeline_container {
  position: relative;
  width: 90%;
  max-width: 1170px;
  margin: 0 auto;
  padding: 2em 0;
}

.Timeline_container::before {
  /* this is the vertical line */
  content: '';
  position: absolute;
  top: 0;
  left: 18px;
  height: 100%;
  width: 4px;
  background: #d7e4ed;
}


